<template>
  <div class="api-config-component">
    <div class="form-field">
      <label>Endpoint URL:</label>
      <input v-model="endpointUrl" type="url" placeholder="https://api.example.com/endpoint" />
    </div>
    <div class="form-field">
      <label>HTTP Method:</label>
      <select v-model="httpMethod">
        <option>GET</option>
        <option>POST</option>
      </select>
    </div>
    <div class="form-field">
      <label>Headers (JSON):</label>
      <textarea v-model="headersText" placeholder='{"Authorization": "Bearer TOKEN", "Accept": "application/json"}'></textarea>
    </div>
    <div class="form-field" v-if="httpMethod === 'POST'">
      <label>Request Body (JSON):</label>
      <textarea v-model="requestBody" placeholder='{"param": "value"}'></textarea>
    </div>
    <div class="form-field">
      <label>Response Format:</label>
      <select v-model="responseFormat">
        <option value="json">JSON</option>
        <option value="xml">XML</option>
        <option value="text">Text</option>
      </select>
    </div>
    <div class="form-field">
      <label>Data Path:</label>
      <input v-model="responseKeyPath" type="text" placeholder="e.g. data.count or price.value" />
    </div>
    <div class="form-field">
      <label>Polling Interval (minutes):</label>
      <input v-model.number="pollInterval" type="number" min="1" />
    </div>
    <div class="form-actions">
      <button @click="sendConfigToDevice">Save</button>
      <button @click="testFetch" :disabled="isTesting">Test Connection</button>
    </div>
    <div class="test-result" v-if="testResult !== null">
      <h4>Test Result:</h4>
      <!-- <pre>{{ testResult }}</pre> -->
      <p>{{ testResult }}</p>
    </div>
    <div class="error" v-if="testError">
      <strong>{{ testError }}</strong>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'

// Definition of reactive state variables for the form
const endpointUrl   = ref('')         // API URL
const httpMethod    = ref('GET')      // HTTP Method
const headersText   = ref('')         // Headers in JSON format (text field)
const requestBody   = ref('')         // Request body (JSON string for POST)
const responseFormat = ref('json')    // Response format: 'json' | 'xml' | 'text'
const responseKeyPath = ref('')       // Key path to the data field
const pollInterval  = ref(10)         // Polling interval (minutes, default 10)

// State variables for testing and messages
const isTesting  = ref(false)         // Flag indicating a test request is in progress
const testResult = ref(null)          // Stores the test result (retrieved value)
const testError  = ref('')            // Stores any test error

// Helper function to extract value from a JSON object by path
function getValueByPath(obj, path) {
  if (!path) return obj;
  return path.split('.').reduce((acc, key) => acc && acc[key] !== undefined ? acc[key] : undefined, obj);
}

// Test function to fetch data from the API
async function testFetch() {
  testError.value = '';
  testResult.value = null;
  isTesting.value = true;
  try {
    // Prepare fetch options
    const options = { method: httpMethod.value, headers: {} };
    // Headers - parse JSON text into an object (if the field is not empty)
    if (headersText.value) {
      try {
        options.headers = JSON.parse(headersText.value);
      } catch(e) {
        throw new Error('Error parsing headers: ' + e.message);
      }
    }
    // Body for POST
    if (httpMethod.value === 'POST') {
      options.headers['Content-Type'] = 'application/json';
      if (requestBody.value) {
        try {
          options.body = JSON.stringify(JSON.parse(requestBody.value));
        } catch(e) {
          throw new Error('Error parsing request body: ' + e.message);
        }
      }
    }
    // Perform fetch
    const res = await fetch(endpointUrl.value, options);
    if (!res.ok) {
      throw new Error(`Error code ${res.status}`);
    }
    // Read and parse response according to the format
    let data;
    if (responseFormat.value === 'json') {
      data = await res.json();
      // Extract nested value
      data = getValueByPath(data, responseKeyPath.value);
    } else if (responseFormat.value === 'text') {
      data = await res.text();
      // For text there is no key, take the whole content or optionally a simple transformation
      // (responseKeyPath can be ignored or used as e.g. substring/regex - optionally)
    } else if (responseFormat.value === 'xml') {
      const text = await res.text();
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(text, "application/xml");
      if (responseKeyPath.value) {
        // Assume a tag name is provided in responseKeyPath for simplicity
        const elem = xmlDoc.getElementsByTagName(responseKeyPath.value)[0];
        data = elem ? elem.textContent : undefined;
      } else {
        // If no specific tag is provided, return the entire XML response as a string
        data = text;
      }
    }
    testResult.value = data;
  } catch (err) {
    testError.value = 'Error: ' + err.message;
  } finally {
    isTesting.value = false;
  }
}

// Function to send configuration to ESP32
async function sendConfigToDevice() {
  // Compose configuration object
  let headersObj = {};
  if (headersText.value) {
    try {
      headersObj = JSON.parse(headersText.value);
    } catch(e) {
      alert("Headers: invalid JSON format");
      return;
    }
  }
  let bodyObj = null;
  if (httpMethod.value === 'POST' && requestBody.value) {
    try {
      bodyObj = JSON.parse(requestBody.value);
    } catch(e) {
      alert("Body: invalid JSON format");
      return;
    }
  }
  const configPayload = {
    endpoint: endpointUrl.value,
    method: httpMethod.value,
    headers: headersObj,
    body: bodyObj,
    responseFormat: responseFormat.value,
    responseKeyPath: responseKeyPath.value,
    interval: pollInterval.value
  };
  try {
    const response = await fetch("http://esp32.local/api/config", {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(configPayload)
    });
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}`);
    }
    alert("Configuration sent successfully.");
  } catch (err) {
    alert("Error sending configuration: " + err.message);
  }
}
</script>

<style>
.api-config-component {
  max-width: 420px;
  background: white;
  text-align: left;
  border-radius: 10px;
}

label {
        color: #aaa;
        display: inline-block;
        margin: 25px 0 15px;
        font-size: 0.6em;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: bold;
    }
    input, select, textarea {
        display: block;
        padding: 10px 6px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid #ddd;
        /* border-bottom: 1px solid #ddd; */
        color: #555;
    }
    button {
        background: #333;
        border: 0;
        padding: 10px 20px;
        margin-top: 20px;
        color: white;
        border-radius: 20px;
    }
.form-field {
}

.form-actions {
  display: flex;
  justify-content: space-between;
}

.test-result {
}

.error {
  color: #ff0062;
  margin-top: 10px;
  font-size: 0.8em;
  font-weight: bold;
}
</style>
