<template>
  <div class="backdrop" @click.self="closeModal">
    <form @submit.prevent="saveConfig">

      <h3>MQTT Configuration</h3>

      <div>
        <input type="checkbox" @input="onInputChange" id="enabled" v-model="config.enabled" />
        <label for="enabled">Enable MQTT</label>
      </div>

      <div .class="settings" v-if="config.enabled === true">
        <div>
          <br>
          <label>Broker Address:</label>
          <input type="text" @input="onInputChange" id="broker" v-model="config.broker" />
        </div>

        <div>
          <label>Port number:</label>
          <input type="number" @input="onInputChange" id="port" v-model.number="config.port"/>
        </div>

        <div>
          <label>Login:</label>
          <input type="text" @input="onInputChange" id="login" v-model="config.login" />
        </div>

        <div>
          <label>Password:</label>
          <input type="password" @input="onInputChange" id="password" v-model="config.password" />
        </div>
      </div>

      <button type="submit" v-if="showSaveButton === true">{{ SaveButtonLabel }}</button>
      
    </form>
  </div>

</template>
  
<script setup>
import { ref, onMounted } from 'vue'
  
  // Configuration object
  const config = ref({
    enabled: false,
    broker: '',
    port: '',
    login: '',
    password: '',
  })

  const showSaveButton = ref(false);
  const SaveButtonLabel = ref('SAVE');
  
  // Message to display feedback
  const message = ref('')

  const emit = defineEmits(['close'])

  function closeModal(e) {
    emit('close')
  }
  
  // Fetch current MQTT configuration from REST endpoint
  const fetchConfig = async () => {
    try {
      const response = await fetch('/api/v1/mqtt')
      if (!response.ok) {
        throw new Error('Failed to fetch configuration')
      }
      const data = await response.json()
      config.value.enabled = data.enabled
      config.value.broker = data.broker
      config.value.port = data.port
      config.value.login = data.login
      config.value.password = data.password
    } catch (error) {
      message.value = error.message
    }
  }

  const onInputChange = () => {
      showSaveButton.value = true
  }
  
  // Save configuration by sending POST request to REST endpoint
  const saveConfig = async () => {
    try {
      const payload = {
        enabled: config.value.enabled,
        host: config.value.broker,
        port: config.value.port,
        login: config.value.login,
        password: config.value.password,
      }
      const response = await fetch('/api/v1/mqtt', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      })
      if (!response.ok) {
        const errText = await response.text()
        throw new Error(errText || 'Failed to save configuration')
      }
      message.value = 'Configuration saved successfully'
      SaveButtonLabel.value = 'SAVED'
    } catch (error) {
      message.value = error.message
    }
  }
  
  onMounted(() => {
    fetchConfig()
  })
  </script>
  
  <style scoped>
      .backdrop{
        top: 0;
        position: fixed;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
    }
    form {
        max-width: 420px;
        margin: 150px auto;
        background: white;
        text-align: left;
        padding: 40px;
        border-radius: 10px;
    }
    h3 {
      color: #333;
      margin: 0 0 15px;
      font-size: 0.9em;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-weight: 700;
    }
    label {
        color: #333;
        display: inline-block;
        margin: 5px 0 0px;
        font-size: 0.6em;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: bold;
    }
    input, select {
        display: inline-block;
        padding: 10px 6px;
        width: 100%;
        box-sizing: border-box;
        border: none;
        border: 1px solid #ddd;
        accent-color: #333;
    }
    input[type="checkbox"] {
        display: inline-block;
        width: 16px;
        margin: 0 10px 0 0;
        position: relative;
        top: 3px;
    }
    button {
      background: #333;
      border: 0;
      padding: 10px 20px;
      margin-top: 20px;
      color: white;
      border-radius: 20px;
      cursor: pointer;
      font-size: 0.7em;
      text-transform: uppercase;
    }
    .submit {
        text-align: center;
        cursor: pointer;
    }
  </style>
  