<template>
  <div class="display-container">
    <!-- Display background image -->
    <img :src="require('@/assets/display.png')" alt="Display background" />

    <!-- Render clickable hotspot for each segment -->
    <div
      v-for="segment in segmentsConfig"
      :key="segment.name"
      class="segment-container"
      :style="{
        top: segment.top,
        left: segment.left,
        width: segment.width,
        height: segment.height
      }"
      @click.stop="toggleSegment(segment.name)"
    >
      <!-- If the segment is active, overlay the active segment image.
            Use horizontal image for segments with rotation 0 and vertical image for others.
            No CSS transform is applied since the image is pre-oriented. -->
            <img
              v-if="activeSegments[segment.name]"
              :src="segment.rotation === 0 ? require('@/assets/segment-h.png') : require('@/assets/segment-v.png')"
              alt="Active segment"
              class="segment-image"
            />
    </div>
  </div>
</template>
  
<script setup>
  import { reactive, ref } from 'vue'

  const props = defineProps({
    initialValue: {
      type: Number,
      default: 0
    }
  })

  // Define the event emitter for sending updated signal value to the parent component
  const emit = defineEmits(['update-signal']);
  // Reactive variable to store the current signal value
  const SignValue = ref(null);
  
  // Configuration for each segment (A to G)
  // For horizontal segments (rotation === 0) the horizontal image is used.
  // For vertical segments (rotation 90 or -90) the vertical image is used.
  // Adjust the top, left, width, and height values as needed to align with your display graphic.
  const segmentsConfig = [
    { name: 'A', top: '19px',  left: '48px',  width: '115px', height: '38px',  rotation: 0 },
    { name: 'B', top: '48px',  left: '154px', width: '38px',  height: '115px', rotation: 90 },
    { name: 'C', top: '183px', left: '154px', width: '38px',  height: '115px', rotation: 90 },
    { name: 'D', top: '289px', left: '48px',  width: '115px', height: '38px',  rotation: 0 },
    { name: 'E', top: '183px', left: '18px',  width: '38px',  height: '115px', rotation: -90 },
    { name: 'F', top: '48px',  left: '18px',  width: '38px',  height: '115px', rotation: -90 },
    { name: 'G', top: '154px', left: '48px',  width: '115px', height: '38px',  rotation: 0 }
  ]
  
  // Reactive object holding the on/off state for each segment
  // const activeSegments = reactive({
  //   A: false,
  //   B: false,
  //   C: false,
  //   D: false,
  //   E: false,
  //   F: false,
  //   G: false
  // })
  const activeSegments = reactive({
    A: Boolean(props.initialValue & (1 << 0)),
    B: Boolean(props.initialValue & (1 << 1)),
    C: Boolean(props.initialValue & (1 << 2)),
    D: Boolean(props.initialValue & (1 << 3)),
    E: Boolean(props.initialValue & (1 << 4)),
    F: Boolean(props.initialValue & (1 << 5)),
    G: Boolean(props.initialValue & (1 << 6))
  })

  // Function to handle signal change and emit the updated value automatically
  const onSignalChange = () => {
    emit('update-signal', SignValue.value);
  };
  
  // Function to toggle the state of a segment (on/off)
  function toggleSegment(segmentName) {
    activeSegments[segmentName] = !activeSegments[segmentName]

    let value = 0;
    if (activeSegments.A) value |= 1 << 0; // Segment A - LSB
    if (activeSegments.B) value |= 1 << 1;
    if (activeSegments.C) value |= 1 << 2;
    if (activeSegments.D) value |= 1 << 3;
    if (activeSegments.E) value |= 1 << 4;
    if (activeSegments.F) value |= 1 << 5;
    if (activeSegments.G) value |= 1 << 6; // Segment G - bit 6

    SignValue.value = value;
    onSignalChange();
    console.log(SignValue.value)
  }
  </script>
  
  <style scoped>
  /* Container for the display; uses relative positioning for overlaying segments */
  .display-container {
    position: relative;
    width: 210px;   /* Width of display-off-v2.png */
    height: 346px;  /* Height of display-off-v2.png */
  }
  
  /* Styling for the base display image */
  .base-display {
    width: 100%;
    height: auto;
    display: block;
  }
  
  /* Container for each clickable segment hotspot */
  .segment-container {
    position: absolute;
    cursor: pointer;
  }
  
  /* Styling for the active segment image */
  .segment-image {
    width: 100%;
    height: 100%;
    pointer-events: none; /* Ensure click is handled by the container */
  }
  </style>
  