<template>
    <div class="backdrop" @click.self="closeModal">
      <div class="modal">
        <form @submit.prevent="save" v-if="mode === 'mqtt'">
            <h3>MQTT Settings</h3>
            <div class="information-style">
                <img :src="require('@/assets/information-icon.png')" alt="Information"/>
                <p>tutaj pisze jakie podpowidzi i jak dziaa ten tryb tutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten tryb</p>
            </div>
            <label for="topicInput">Topic:</label>
            <input type="text" id="topicInput" v-model="topicValue" required>
            <button type="submit">SAVE</button>
        </form>
        <form @submit.prevent="save" v-if="mode === 'clock'">
            <h3>Clock Settings</h3>
            <div class="information-style">
                <img :src="require('@/assets/information-icon.png')" alt="Information"/>
                <p>tutaj pisze jakie podpowidzi i jak dziaa ten tryb tutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten tryb</p>
            </div>
            <TimeMode />
            <button type="submit">SAVE</button>
        </form>
        <form @submit.prevent="save" v-if="mode === 'timer'">
            <h3>Timer Settings</h3>
            <div class="information-style">
                <img :src="require('@/assets/information-icon.png')" alt="Information"/>
                <p>tutaj pisze jakie podpowidzi i jak dziaa ten tryb tutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten tryb</p>
            </div>

        </form>
        <form @submit.prevent="save" v-if="mode === 'custom-api'">
            <h3>Custom API (advanced)</h3>
            <div class="information-style">
                <img :src="require('@/assets/information-icon.png')" alt="Information"/>
                <p>tutaj pisze jakie podpowidzi i jak dziaa ten tryb tutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten trybtutaj pisze jakie podpowidzi i jak dziaa ten tryb</p>
            </div>
            <GeneralApi />
        </form>

        <!-- <h3>{{ title }}</h3> -->
        <!-- <pre>{{ aggregatedData }}</pre> -->
        <!-- <button @click="save">SAVE</button> -->
      </div>
    </div>
</template>
  
<script setup>
  import { defineProps, defineEmits, ref } from 'vue'
  import TimeMode from './TimeMode.vue'
  import GeneralApi from './GeneralApi.vue';
  
  const props = defineProps({
    groupKey: {
      type: String,
      required: true
    },
    mode: {
      type: String,
      required: true
    },
    aggregatedData: {
      type: Object,
      required: true
    }
  })
  
  const emits = defineEmits(['close', 'save'])
  
  // Reactive variable storing the value entered by the user
  const topicValue = ref('')

  // Function to close the modal
  const closeModal = () => {
    emits('close')
  }

  // Save function
  const save = () => {
    if (props.mode === 'mqtt') {
      // List of required keys
      const requiredKeys = ['start_position', 'end_position', 'pattern', 'separator', 'mode']
      // Get group data
      const groupData = props.aggregatedData.groups[props.groupKey]

      // Remove keys that are not required
      Object.keys(groupData).forEach(key => {
        if (!requiredKeys.includes(key)) {
          delete groupData[key]
        }
      })

      // Add a new "topic" key with the value taken from the input
      groupData.topic = topicValue.value
    }
    // For other modes, appropriate logic can be added here

    // Emit the save event with updated aggregatedData
    emits('save', props.aggregatedData)
  }
</script>
  
<style scoped>
