<template>
  <div class="backdrop" @click.self="closeModal">
    <div class="update-check">
      <h3>Versions</h3>
      <div class="label">
        <p>
          Firmware:
          {{ installedFirmwareVersion }}
          <span
            v-if="firmwareState === 'update'"
            style="color: green"
          >({{ availableFirmwareVersion }})</span>
          <span
            v-else-if="firmwareState === 'offline'"
            style="color: red"
          >(N/A)</span>
          |
          Web App:
          {{ installedWebAppVersion }}
          <span
            v-if="webAppState === 'update'"
            style="color: green"
          >({{ availableWebAppVersion }})</span>
          <span
            v-else-if="webAppState === 'offline'"
            style="color: red"
          >(N/A)</span>
        </p>
      </div>

      <div v-if="!showProgress">
        <button v-if="updateAvailable" @click="startUpdate">UPDATE AVAILABLE</button>
      </div>

      <div  class="progress-container" v-if="showProgress">
        <div class="label">{{ updateLabel }}</div>
          <div class="progress-wrapper">
            <progress max="100" :value="progress"></progress>
            <div class="progress-percent">{{ progress }}%</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
  import { ref, onMounted } from 'vue';

  // Reactive state variables
  const installedFirmwareVersion = ref('');
  const installedWebAppVersion = ref('');
  const availableFirmwareVersion = ref('');
  const availableWebAppVersion = ref('');
  const updateAvailable = ref(false);
  const firmwareState = ref(''); // '', 'update', 'offline'
  const webAppState = ref(''); // '', 'update', 'offline'
  const updateLabel = ref('');
  const progress = ref(0);
  const showProgress = ref(false);
  const message = ref('');
  const updateInProgress = ref(false)

  const emit = defineEmits(['close'])

  function closeModal(e) {
      emit('close')
    }

  // Function to fetch versions from ESP32
  async function fetchInstalledVersions() {
    try {
      const response = await fetch('/api/v1/versions');
      //const response = await fetch('http://localhost:3000/versions');
      const data = await response.json();
      installedFirmwareVersion.value = data.versions.firm_version;
      installedWebAppVersion.value = data.versions.web_app_version;
    } catch (error) {
      message.value = 'Error fetching installed versions';
      console.error('Error fetching installed versions:', error);
    }
  }

  // Function to fetch available versions from external OTA server
  async function fetchAvailableVersions() {
    try {
      const response = await fetch('https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/version.json');
      const data = await response.json();
      availableFirmwareVersion.value = data.firmware_version;
      availableWebAppVersion.value = data.web_app_version;
    } catch (error) {
      firmwareState.value = 'offline';
      webAppState.value = 'offline';
      return;
    }
    firmwareState.value = installedFirmwareVersion.value !== availableFirmwareVersion.value ? 'update' : '';
    webAppState.value = installedWebAppVersion.value !== availableWebAppVersion.value ? 'update' : '';

    updateAvailable.value = firmwareState.value === 'update' || webAppState.value === 'update';
  }

  // Function to check if an update is needed
  function checkForUpdate() {
    // Compare installed and available versions
    if (
      (installedFirmwareVersion.value !== availableFirmwareVersion.value ||
      installedWebAppVersion.value !== availableWebAppVersion.value) && 
      (firmwareState.value !== 'offline' && 
      webAppState.value !== 'offline')
    ) {
      updateAvailable.value = true;
    } else {
      updateAvailable.value = false;
    }
  }

  // Function to start progress polling
  let progressInterval = null;
  function startProgressPolling() {
    progressInterval = setInterval(async () => {
      try {
        const response = await fetch('/api/v1/ota/progress');
        const data = await response.json();
        progress.value = data.progress;

        if (progress.value > 0 && !updateInProgress.value) {
          updateInProgress.value = true;
        }

        if (updateInProgress.value && progress.value === 0) {
          clearInterval(progressInterval);
          updateInProgress.value = false;
          window.location.reload(); 
        }
      } catch (error) {
        console.error('Error polling OTA progress:', error);
      }
    }, 1000);
  }

  // Function to initiate OTA update based on version differences
  async function startUpdate() {
    showProgress.value = true;
    progress.value = 0;

    // Determine which components need update
    const updateFirmware = installedFirmwareVersion.value !== availableFirmwareVersion.value;
    const updateWebApp = installedWebAppVersion.value !== availableWebAppVersion.value;

    // If both updates are needed, call combined endpoint
    if (updateFirmware && updateWebApp) {
      updateLabel.value = 'Updating Web-App and Firmware...';
      try {
        const response = await fetch('/api/v1/ota/both', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            firmware_url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/firmware_v${availableFirmwareVersion.value}.bin`,
            web_app_url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/web_app_v${availableWebAppVersion.value}.bin`
          })
        });
        const text = await response.text();
        message.value = text;
        startProgressPolling();
      } catch (error) {
        message.value = 'Error starting combined OTA update';
        console.error('Combined OTA error:', error);
      }
    } else if (updateFirmware) {
      // Only firmware update is needed
      updateLabel.value = 'Updating firmware...';
      try {
        const response = await fetch('/api/v1/ota/firmware', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/firmware_v${availableFirmwareVersion.value}.bin`
          })
        });
        const text = await response.text();
        message.value = text;
        startProgressPolling();
      } catch (error) {
        message.value = 'Error starting firmware OTA update';
        console.error('Firmware OTA error:', error);
      }
    } else if (updateWebApp) {
      // Only web app update is needed
      updateLabel.value = 'Updating Web-App...';
      try {
        const response = await fetch('/api/v1/ota/web_app', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            url: `https://download.smartsolutions4home.com/ota-firmware/7-flip-firmware/web_app_v${availableWebAppVersion.value}.bin`
          })
        });
        const text = await response.text();
        message.value = text;
        startProgressPolling();
      } catch (error) {
        message.value = 'Error starting Web-App OTA update';
        console.error('Web-App OTA error:', error);
      }
    }
  }

  onMounted(async () => {
    await fetchInstalledVersions();
    await fetchAvailableVersions();
    console.log(installedFirmwareVersion.value, availableFirmwareVersion.value, installedWebAppVersion.value, availableWebAppVersion.value);
    checkForUpdate();
  });
</script>
  
<style scoped>
.backdrop{
    top: 0;
    position: fixed;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
}
.update-check {
  max-width: 420px;
  margin: 150px auto;
  background: white;
  text-align: left;
  padding: 40px;
  border-radius: 10px;
}

.label {
  color: #333;
  display: inline-block;
  font-size: 0.7em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}

h3 {
      color: #333;
      margin: 0 0 5px;
      font-size: 0.9em;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-weight: 700;
    }

button {
  background: #40464e;
  border: 0;
  padding: 10px 20px;
  margin-top: 20px;
  color: white;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.7em;
  text-transform: uppercase;
}

.progress-container {
  display: flex;
  margin-top: 16px;
  flex-direction: column;
  align-items: flex-start;
}

.progress-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
}

progress {
  width: 390px;
  height: 10px;
  border: none;
  border-radius: 5px;
  -webkit-appearance: none;
  appearance: none;
}

/* Chrome, Safari, Opera */
progress::-webkit-progress-bar {
  background-color: #e6e6e6;
  border-radius: 10px;
}

progress::-webkit-progress-value {
  background-color: #333;
  border-radius: 10px;
}

/* Firefox */
progress::-moz-progress-bar {
  background-color: #333;
  border-radius: 10px;
}
</style>  