<template>
  <div class="time-check">
    <!-- Dropdown list for selecting the timezone -->
    <label for="timezone-select">Select Timezone: </label>
    <select id="timezone-select" v-model="selectedTimezone" @change="updateTime">
      <option v-for="tz in timezones" :key="tz" :value="tz">
        {{ tz }}
      </option>
    </select>

    <!-- Display the current time data -->
    <div v-if="timeData" class="current-time-style">
      <p>Cuttern time & date:</p>
      <p class="time">
        {{ timeData.hour }}:{{ timeData.minute }}&nbsp;
        {{ timeData.day }}.{{ timeData.month }}.{{ timeData.year }}
      </p>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'

// Reactive state to store the list of timezones
const timezones = ref([])
// Reactive variable for the selected timezone
const selectedTimezone = ref('')
// Reactive state to store the current time data
const timeData = ref(null)
// Timer variable for automatic time refresh
let timer = null

// Function to refresh the current time based on the selected timezone (without sending to server)
const refreshTime = () => {
  // Use Intl.DateTimeFormat to format the date for the selected timezone
  const dtf = new Intl.DateTimeFormat('en-US', {
    timeZone: selectedTimezone.value,
    year: 'numeric',
    month: 'numeric',
    day: 'numeric',
    hour: 'numeric',
    minute: 'numeric',
    hour12: false
  })
  // Extract date parts using formatToParts
  const parts = dtf.formatToParts(new Date())
  const data = {}
  parts.forEach(part => {
    if (part.type === 'year') {
      data.year = parseInt(part.value, 10)
    } else if (part.type === 'month') {
      data.month = parseInt(part.value, 10)
    } else if (part.type === 'day') {
      data.day = parseInt(part.value, 10)
    } else if (part.type === 'hour') {
      data.hour = parseInt(part.value, 10)
    } else if (part.type === 'minute') {
      data.minute = parseInt(part.value, 10)
    }
  })
  timeData.value = data
}

// Function to update time and send the time data when timezone is changed
const updateTime = async () => {
  // Update the time data using the selected timezone
  refreshTime()
  // Create an array with the time data in the order: [minute, hour, day, month, year]
  const timeArray = [
    timeData.value.minute,
    timeData.value.hour,
    timeData.value.day,
    timeData.value.month,
    timeData.value.year
  ]
  // Send the time data to the REST server endpoint
  await sendTimeData(timeArray)
}

// Function to send the time data array to the REST server endpoint
const sendTimeData = async (time) => {
  try {
    const response = await fetch('/api/v1/time', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(time)
    })

    if (!response.ok) {
      throw new Error('Network response was not ok')
    }
    console.log('Time data sent successfully')
  } catch (error) {
    console.error('Error sending time data:', error)
  }
}

// On component mount, load the list of timezones, update time for the default selection,
// and set up an interval to refresh the time automatically
onMounted(() => {
  // Try to use Intl.supportedValuesOf if available (experimental)
  if (typeof Intl.supportedValuesOf === 'function') {
    timezones.value = Intl.supportedValuesOf('timeZone')
  } else {
    // Fallback to a static list of common time zones
    timezones.value = [
      'Europe/London',
      'Europe/Amsterdam',
      'America/New_York',
      'Asia/Tokyo',
      'Australia/Sydney'
    ]
  }
  
  // Set default selected timezone if the list is not empty
  if (timezones.value.length > 0) {
    selectedTimezone.value = timezones.value[0]
    updateTime() // Initial update and send data to server
  }
  
  // Set up a timer to refresh the time automatically every second
  timer = setInterval(() => {
    refreshTime()
  }, 1000)
})

// Clear the timer when the component is unmounted
onUnmounted(() => {
  clearInterval(timer)
})
</script>

<style scoped>
.time-check {
  color: #333;
  /* display: inline-block; */
  font-size: 0.8em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
}
.current-time-style {
  display: flex;
  align-items: center;
  vertical-align: middle;
  gap: 10px;
}
.time {
  font-size: 20px;
  font-weight: bold;
  color: black;
  vertical-align: middle;
}
</style>
