<template>
  <div class="display-wrapper" ref="containerRef">
    <!-- Inline container for displays and inline separators -->
    <div class="display-inline" v-if="DisplayCount > 0">
      <template v-for="index in displayIndices" :key="index">
        <!-- Single display component with update-signal event handler -->
        <SingleDisplay :initialValue="getInitialValueForDisplay(index)" @update-signal="handleUpdateSignal(index, $event)" />
        <!-- Render inline separator if not the last display and if the separator is not 'plus' -->
        <img
          v-if="index < DisplayCount && separators[index - 1] !== 'plus'"
          :src="require(`@/assets/${separators[index - 1]}.png`)"
          alt="separator"
          class="inline-separator"
          @click="separatorClicked(index - 1)"
        />
      </template>
    </div>

    <!-- Overlay plus icons rendered on top of displays (only when separator === 'plus') -->
    <div class="plus-overlay" v-if="DisplayCount > 0">
      <template v-for="(sep, index) in separators" :key="'overlay-' + index">
        <img
          v-if="sep === 'plus'"
          :src="require(`@/assets/plus.png`)"
          alt="plus"
          class="plus-icon"
          :style="plusStyle(index)"
          @click.stop="openContextMenu($event, index)"
        />
      </template>
    </div>

    <!-- Context menu for selecting a separator -->
    <div
      v-if="contextMenu.visible"
      class="context-menu"
      :style="{ top: contextMenu.y + 'px', left: contextMenu.x + 'px' }"
    >
      <div
        v-for="option in options"
        :key="option"
        class="context-menu-option"
        @click="selectSeparator(option)"
      >
        {{ option }}
      </div>
    </div>

    <!-- Optional loading state -->
    <div v-if="DisplayCount === 0">
      Loading...
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch, onMounted } from 'vue';
import SingleDisplay from './SingleDisplay.vue'; // Importing the 7-segment display component

// Reference to the main container for converting viewport coordinates to relative coordinates
const containerRef = ref(null);

// Reactive variable for JSON settings fetched from the server
const jsonSettings = ref(null);

// Reactive variable for total number of displays (DisplayCount)
const DisplayCount = ref(0);

// Reactive array for separators between displays (length = DisplayCount - 1)
// Default value for each gap will be 'plus', a boundary between grup is ustawiana na podstawie JSON
const separators = ref([]);

// Reactive array to hold the signal values from each SingleDisplay
const SignValues = ref([]);

// Available separator options
const options = ['space', 'blank', 'colon', 'dot', 'dash'];

// State for the context menu (separator selection)
const contextMenu = ref({
  visible: false,
  x: 0,
  y: 0,
  index: null, // Index of the gap currently being edited
});

//Initial value of the single display
const getInitialValueForDisplay = (index) => {
  // If the settings have not been fetched yet, return 0
  if (!jsonSettings.value) return 0;
  
  // Convert the display number (1, 2, 3, ...) to a zero-based index
  const idx = index - 1;
  
  // Retrieve all groups and sort them by start_position
  const groups = Object.values(jsonSettings.value.groups)
    .sort((a, b) => a.start_position - b.start_position);
  
  // Iterate over the sorted groups
  for (const group of groups) {
    // If the display index falls within the group's range (start_position to end_position)
    if (idx >= group.start_position && idx <= group.end_position) {
      // Calculate the local display index within the group
      const localIndex = idx - group.start_position;
      // Return the value for the key in the pattern (e.g., "disp0", "disp1", ...)
      return group.pattern[`disp${localIndex}`] || 0;
    }
  }
  
  // If no matching group is found, return 0 as the default value
  return 0;
};

// Function to fetch the display settings from the endpoint
async function fetchDisplaySettings() {
  try {
    const response = await fetch('http://localhost:3000/display_settings');
    //const response = await fetch('/api/v1/config');
    if (!response.ok) {
      throw new Error('Network response was not ok: ' + response.statusText);
    }
    const data = await response.json();
   
    jsonSettings.value = data;

    // Calculate total number of displays as: max(end_position) + 1 (positions count od prawej strony)
    const groupValues = Object.values(jsonSettings.value.groups);
    const count = Math.max(...groupValues.map(g => g.end_position)) + 1;
    DisplayCount.value = count;

    // Initialize separators array with default value 'plus'
    separators.value = Array(count - 1).fill('plus');

    // For each group (except group0 which always ma separator null), update separator at the group boundary
    // Boundary: gap before the first display of the group (i.e. at index = start_position - 1)
    for (const key in jsonSettings.value.groups) {
      const group = jsonSettings.value.groups[key];
      if (group.start_position > 0 && group.separator) {
        separators.value[group.start_position - 1] = group.separator;
      }
    }

    console.log(data);
    console.log(data.value);

    // Initialize SignValues array with null for each display
    SignValues.value = Array(count).fill(null);

    console.log(SignValues);
    console.log(SignValues.value);

  } catch (error) {
    console.error('Error fetching display settings:', error);
  }
}

// Fetch settings on component mount
onMounted(() => {
  fetchDisplaySettings();
});

// Computed array of display indices [1, 2, ..., DisplayCount]
const displayIndices = computed(() =>
  Array.from({ length: DisplayCount.value }, (_, i) => i + 1)
);

// Function to handle update-signal event from SingleDisplay component
function handleUpdateSignal(index, newValue) {
  SignValues.value[index - 1] = newValue;
}

// Close dropdown for given group key when kursor opuszcza obszar menu
const closeDropdown = (groupKey) => {
  dropdownOpen.value[groupKey] = false
}

// Function to aggregate SignValues into grouped data based on separators
function aggregateDisplaySettings() {
  const totalDisplays = DisplayCount.value;
  const computedGroups = [];
  let currentGroupStart = 0;

  // Divide into groups  iterate through all gaps between displays
  for (let i = 0; i < totalDisplays - 1; i++) {
    if (separators.value[i] !== 'plus') {
      const groupPattern = {};
      for (let j = currentGroupStart; j <= i; j++) {
        //  If the value is null, get the default value from the original JSON
        const currentValue = SignValues.value[j];
        groupPattern[`disp${j - currentGroupStart}`] = 
          currentValue !== null ? currentValue : getInitialValueForDisplay(j + 1);
      }
      computedGroups.push({
        start: currentGroupStart,
        end: i,
        pattern: groupPattern,
        separator: currentGroupStart > 0 ? separators.value[currentGroupStart - 1] : null,
      });
      currentGroupStart = i + 1;
    }
  }
  // Last group  from currentGroupStart to the last display
  const lastPattern = {};
  for (let j = currentGroupStart; j < totalDisplays; j++) {
    const currentValue = SignValues.value[j];
    lastPattern[`disp${j - currentGroupStart}`] = 
      currentValue !== null ? currentValue : getInitialValueForDisplay(j + 1);
  }
  computedGroups.push({
    start: currentGroupStart,
    end: totalDisplays - 1,
    pattern: lastPattern,
    separator: currentGroupStart > 0 ? separators.value[currentGroupStart - 1] : null,
  });

  // Get original groups from JSON and sort them by start_position
  const originalGroups = jsonSettings.value.groups;
  const originalGroupsSorted = Object.entries(originalGroups).sort(
    (a, b) => a[1].start_position - b[1].start_position
  );

  // Build new groups by comparing boundaries with the original
  const updatedGroups = {};
  for (let i = 0; i < computedGroups.length; i++) {
    const computed = computedGroups[i];

    // Search for an original group with exactly the same boundaries
    let matchingOriginal = null;
    for (const [key, origGroup] of originalGroupsSorted) {
      if (origGroup.start_position === computed.start && origGroup.end_position === computed.end) {
        matchingOriginal = origGroup;
        break;
      }
    }

    let newGroup = {};
    if (matchingOriginal) {
      // Group was not changed  copy mode and other settings
      newGroup = { ...matchingOriginal };
      newGroup.start_position = computed.start;
      newGroup.end_position = computed.end;
      newGroup.pattern = computed.pattern;
      newGroup.separator = computed.start > 0 ? separators.value[computed.start - 1] : null;
    } else {
      // Group was modified  set mode to null and remove additional settings
      newGroup = {
        start_position: computed.start,
        end_position: computed.end,
        pattern: computed.pattern,
        separator: computed.start > 0 ? separators.value[computed.start - 1] : null,
        mode: null,
      };
    }
    updatedGroups[`group${i}`] = newGroup;
  }

  // Final JSON object  "general" section remains unchanged (except for updating the number of groups)
  const newJson = {
    general: { ...jsonSettings.value.general, groups: computedGroups.length },
    groups: updatedGroups,
  };

  return newJson;
}

// Emit aggregated display data to parent component whenever SignValues or separators change
const emit = defineEmits(['update-display-data']);
watch(
  [SignValues, separators],
  () => {
    const aggregatedData = aggregateDisplaySettings();
    emit('update-display-data', aggregatedData);
    //console.log('Emitted aggregated data:', aggregatedData);
  },
  { deep: true }
);

// Constants for sizes (in pixels)
const displayWidth = 210;
const plusWidth = 30;
const displayHeight = 346; // Height of display component
const plusTop = (displayHeight - plusWidth) / 2; // Center plus vertically

// Function to compute inline style for plus icons, considering previous non-plus separators
function plusStyle(index) {
  let offset = 0;
  // Sum the widths of all non-plus separators before this gap
  for (let i = 0; i < index; i++) {
    if (separators.value[i] !== 'plus') {
      offset += 106; // width of non-plus separator image
    }
  }
  // Calculate left position:
  // left edge of display (index + 1) = (index + 1) * displayWidth + offset, then subtract half of plusWidth for centering
  const left = ((index + 1) * displayWidth) + offset - plusWidth / 2;
  return { left: left + 'px', top: plusTop + 'px' };
}

// Function to open context menu when clicking a plus icon
function openContextMenu(event, index) {
  event.preventDefault();
  const rect = containerRef.value.getBoundingClientRect();
  contextMenu.value = {
    visible: true,
    x: event.clientX - rect.left,
    y: event.clientY - rect.top,
    index: index,
  };
}

// Function to update the separator when an option is selected
function selectSeparator(option) {
  if (contextMenu.value.index !== null) {
    // Update the separator for the selected gap (triggering inline rendering)
    separators.value[contextMenu.value.index] = option;
  }
  contextMenu.value.visible = false;
  contextMenu.value.index = null;
}

// Function to handle click on an inline separator (non-plus)
// Clicking on an active separator resets it to 'plus'
function separatorClicked(index) {
  separators.value[index] = 'plus';
}

// Global click listener to close the context menu when clicking outside
document.addEventListener('click', () => {
  contextMenu.value.visible = false;
});
</script>

<style scoped>
.display-wrapper {
  position: relative;
  width: fit-content;
  margin: 0 auto;
}

/* Container for displays arranged inline */
.display-inline {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

/* Inline separator (non-plus) styles */
.inline-separator {
  width: 106px;
  cursor: pointer;
}

/* Container for plus icons overlayed on displays */
.plus-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Only plus icons receive pointer events */
}

/* Plus icon styles */
.plus-icon {
  position: absolute;
  width: 30px;
  cursor: pointer;
  pointer-events: auto; /* Enable click events on plus icons */
}

/* Context menu styles */
.context-menu {
  position: absolute;
  border: 1px solid #ccc;
  background-color: #fff;
  border-radius: 5px;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  font-size: 0.8em;
  text-transform: uppercase;
  width: 85px;
  color: #222222;
  letter-spacing: 1px;
  font-weight: 700;
}

.context-menu :hover {
  background-color: #b8b8b8;
}

.context-menu-option {
  padding: 5px;
  cursor: pointer;
}
</style>
